/**
 * 
 */
import React from "react";
import { memo, useState } from "react";
import "./SignalTree.css";
import vehicleApi from "./VssAPI.json";
import { 
	SIGNAL_TYPES, 
	setSignals, getSignals, 
	subscribeSignals, unsubscribeSignals 
} from "./WsReq";

/* global BigInt */

const SIGNAL_VIEWER_CHANNEL = "signalviewer";


class VssTree extends React.Component {

	selectedListItem = null;
	oldPath = null;
	canSignalMap = new Map();
	
	constructor(props) {
        super(props);
		this.state = {
			path: "Vehicle",
			name: "Vehicle",
			type: vehicleApi.Vehicle.type ? vehicleApi.Vehicle.type : "-",
			description: vehicleApi.Vehicle.description ? vehicleApi.Vehicle.description : "-",
			datatype: "-",
			default: "-",
			min: "-",
			max: "-",
			unit: "-",
			comment: vehicleApi.Vehicle.comment ? vehicleApi.Vehicle.comment : "-",
			deprecation: vehicleApi.Vehicle.deprecation ? vehicleApi.Vehicle.deprecation : "-",
			allowed: "-"
		};

		if (props.signalDB != null) {
			props.signalDB.pdus.forEach((pdu) => {
				pdu.signals.forEach((signal) => {
					this.canSignalMap.set(signal.name, signal);
				});
			});
		}
    }

	handleTreeEvent(treeItem, node, path, name) {
		if (path !== 'Vehicle') {
			treeItem.parentElement.querySelector(".nested").classList.toggle("active");
			treeItem.classList.toggle("caret-down");
		}
		
		this.setState({
			path: path,
			name: name,
			type: node.type ? node.type : "-",
			description: node.description ? node.description : "-",
			datatype: "-",
			default: "-",
			min: "-",
			max: "-",
			unit: "-",
			comment: node.comment ? node.comment : "-",
			deprecation: node.deprecation ? node.deprecation : "-",
			allowed: "-",
		});

		if (this.selectedListItem) {
			if (this.oldPath !== 'Vehicle') {
				this.selectedListItem.setAttribute("style", "color: black; cursor: pointer;");
			}
			else {
				this.selectedListItem.setAttribute("style", "color: black; font-size: 22px; cursor: pointer;");
			}
		}

		if (path !== 'Vehicle') {
			treeItem.setAttribute("style", "color: blue; font-weight: 700; cursor: pointer;");
		}
		else {
			treeItem.setAttribute("style", "color: blue; font-size: 22px; font-weight: 700; cursor: pointer;");
		}

		this.selectedListItem = treeItem;
		this.oldPath = path;
	}

	getList(node, path) {
		const children = node['children'];
		return (
			<ul>
				{Object.keys(children).map((key) =>
					this.getSubTree(children[key], path + "." + key, key)
				)}
			</ul>
		);
	}

	getSignalCount(node) {
		let size = 0;
		const children = node['children'];
		if (children) {
			Object.values(children).forEach((subnode) => {
				size += this.getSignalCount(subnode);
			});
		}
		else {
			size = 1;
		}
		return size;
	}

	getSubTree(node, path, name) {
		const children = node['children'];
		return (
			<li key={path}>
				{node['type'] === 'branch' ?
					<>
						<span className="caret" onClick={(e) => this.handleTreeEvent(e.target, node, path, name)} >
							{name}
						</span>
						<span>
							&nbsp;({this.getSignalCount(node)})
						</span>
						<ul className="nested">
							{Object.keys(children).map((key) =>
								this.getSubTree(children[key], path + "." + key, key)
							)}
						</ul>
					</>
					:
					<>
						<span onClick={(e) => this.handleSignal(e.target, node, path, name)} style={{ cursor: "pointer" }} >
							{name}
						</span>
					</>
				}
			</li>
		);
	}

	handleSignal(listItem, signal, path, name) {

		if (this.selectedListItem) {
			this.selectedListItem.setAttribute("style", "color: black; cursor: pointer;");
		}
		listItem.setAttribute("style", "color: blue; font-weight: 700; cursor: pointer;");
		this.selectedListItem = listItem;

		this.setState({
			path: path,
			name: name,
			type: signal.type ? signal.type : "-",
			description: signal.description ? signal.description : "-",
			datatype: signal.datatype ? signal.datatype : "-",
			default: signal.default ? signal.default : "-",
			min: signal.min ? signal.min : "-",
			max: signal.max ? signal.max : "-",
			unit: signal.unit ? signal.unit : "-",
			comment: signal.comment ? signal.comment : "-",
			deprecation: signal.deprecation ? signal.deprecation : "-",
			allowed: signal.allowed ? signal.allowed : "-",
			mapping: signal.mapping ? signal.mapping : "-"
		});

		unsubscribeSignals({ channel: SIGNAL_VIEWER_CHANNEL });
		const jsonData = {
			channel: SIGNAL_VIEWER_CHANNEL,
			signals: [
				{ 
					name: path,
					type: SIGNAL_TYPES.VSS
				}
			]
		};
		const canSignalName = signal.mapping;
		if (canSignalName) {
			jsonData.signals.push(
				{
					name: canSignalName,
					type: SIGNAL_TYPES.RAW
				}
			);
		}
		subscribeSignals(
			jsonData,
			(json) => {
				if (json && json.signals && json.signals.length > 0) {
					const signal = json.signals[0];
					if (signal.type === SIGNAL_TYPES.VSS) {
						if (signal.name === path) {
							this.updateVssField(signal, path);
						}
						
					}
					else if (signal.type === SIGNAL_TYPES.RAW) {
						if (signal.name === canSignalName) {
							this.updateCanField(signal, canSignalName);
						}
					}
				}
			});
	}

	componentWillUnmount() {
		unsubscribeSignals({ channel: SIGNAL_VIEWER_CHANNEL });
	}

	getAllowedList() {
		if (this.state.allowed === "-") {
			return "-";
		}
		else {
			if (this.state.allowed && this.state.allowed.length !== 0) {
				return (
					<select style={{width: "100%"}} >
						{this.state.allowed.map((label) => 
							<option key={label}>{label}</option>
						)}
					</select>
				);
			}
			else {
				return "-";
			}
		}
	}

	setVssValue(path) {
		let value = null;
		let tag = document.getElementById("vss-value-text");
		if (tag) {
			value = tag.value;
		}
		else {
			tag = document.getElementById("vss-value-combo");
			if (tag) {
				const options = tag.getElementsByTagName("option");
				if (options && options.length > 1) {
					if (tag.selectedIndex > 0 && tag.selectedIndex < options.length) {
						value = options[tag.selectedIndex].innerText;
					}
				}
			}
		}
		if (value != null) {
			const jsonData = {
				signals: [
					{ 
						name  : path, 
						value : value,
						type  : SIGNAL_TYPES.VSS
					}
				]
			};
			//console.log(jsonData.signals[0]);
			setSignals(jsonData);
		}
	}

	getVssValue(path) {
		const jsonData = {
			signals: [
				{ 
					name: path,
					type: SIGNAL_TYPES.VSS
				}
			]
		};
		getSignals(jsonData, (json) => this.updateVssField(json, path));
	}

	updateVssField(signal, path) {
		this.setState({
			value: signal.value
		});
		let tag = document.getElementById("vss-value-text");
		if (tag) {
			tag.value = signal.value;
		}
		else {
			tag = document.getElementById("vss-value-combo");
			if (tag) {
				const options = tag.getElementsByTagName("option");
				for (let i = 0; i < options.length; i++) {
					if (options[i].innerText === signal.value) {
						tag.selectedIndex = i;
						break;
					}
				}
			}
		}
	}

	renderVssValueField() {
		if (this.state.datatype === "boolean") {
			return (
				<select id="vss-value-combo"
					onChange={() => this.setVssValue(this.state.path) }
				>
					<option key="undefined">undefined</option>
					<option key="True">True</option>
					<option key="False">False</option>
				</select>
			);			
		}
		else if (this.state.allowed && this.state.allowed !== "-") {
			return (
				<select id="vss-value-combo"
					onChange={() => this.setVssValue(this.state.path) }
				>
					<option key="undefined">undefined</option>
					{
						this.state.allowed.map((key) =>
							<option key={key}>
								{key}
							</option>
						)
					}
				</select>
			);
		}
		else {
			return (
				<input type="text" defaultValue={this.state.value} id="vss-value-text"
					onKeyDown={(e) => {
						if (e.key === 'Enter') {
							this.setVssValue(this.state.path);
						}
					}}>
				</input>
			);
		}
	}

	setCanValue(name) {
		let value = null;
		let tag = document.getElementById("can-value-text");
		if (tag) {
			value = Number(tag.value);
		}
		else {
			tag = document.getElementById("can-value-combo");
			if (tag) {
				const options = tag.getElementsByTagName("option");
				if (options && options.length > 0) {
					if (tag.selectedIndex > -1 && tag.selectedIndex < options.length) {
						let enumVal = options[tag.selectedIndex].innerText.split(":")[0];
						enumVal = enumVal.trim();
						value = Number(enumVal);
					}
				}
			}
		}
		if (value != null) {
			const jsonData = {
				signals: [
					{ 
						name  : name, 
						value : value,
						type  : SIGNAL_TYPES.RAW
					}
				]
			};
			//console.log(jsonData.signals[0]);
			setSignals(jsonData);
		}
	}

	getCanValue(name) {
		const jsonData = {
			signals: [
				{ 
					name: name,
					type: SIGNAL_TYPES.RAW
				}
			]
		};
		getSignals(jsonData, (json) => this.updateCanField(json, name));
	}

	updateCanField(signal, name) {
		if (this.state.canValue !== signal.value) {
			//console.log(canSignal);
			this.setState({
				canValue: signal.value
			});
			let tag = document.getElementById("can-value-text");
			if (tag) {
				tag.value = signal.value;
			}

			tag = document.getElementById("can-raw-value-text");
			if (tag) {
				tag.innerText = physToRaw(signal.value, signal);
			}

			tag = document.getElementById("can-value-combo");
			if (tag) {
				const options = tag.getElementsByTagName("option");
				for (let i = 0; i < options.length; i++) {
					let enumVal = options[i].innerText.split(":")[0];
					enumVal = Number(enumVal.trim());
					if (enumVal === signal.value) {
						tag.selectedIndex = i;
						break;
					}
				}
			}
		}
	}

	renderCanValueField(name) {
		return (
			<EcuSignal signal={this.canSignalMap.get(name)}/>
		);
	}
   
	render() {
		return (
			<div className="w3-row-padding w3-margin-top">
				<div className="w3-col w3-container" style={{ width: "40%" }}>
					<ul id="myUL">
						<li key={"Vehicle"} >
							<span
								onClick={(e) => this.handleTreeEvent(e.target, vehicleApi.Vehicle, "Vehicle")}
								style={{ cursor: "pointer", fontSize: "22px" }}
							>
								{"\u25B6"}{"Vehicle"}
							</span>
							<span>
								&nbsp;({this.getSignalCount(vehicleApi.Vehicle)})
							</span>
							{this.getList(vehicleApi.Vehicle, "Vehicle")}
						</li>
					</ul>
				</div>
				<div className="w3-col w3-container MyStckyHeader" style={{width:"60%"}}>
					<div className="w3-card-4">
						<header className="w3-container w3-blue">
							<h2>{this.state.path}</h2>
						</header>

						<div className="w3-container">
							<table className="w3-table">
								<tbody>
								<tr>
									<td><b>path</b></td>
									<td><b>{this.state.path}</b></td>
								</tr>
								<tr>
									<td><b>description</b></td>
									<td>{this.state.description}</td>
								</tr>
								<tr>
									<td><b>type</b></td>
									<td>{this.state.type}</td>
								</tr>
								<tr>
									<td><b>datatype</b></td>
									<td>{this.state.datatype}</td>
								</tr>
								<tr>
									<td><b>default</b></td>
									<td>{this.state.default}</td>
								</tr>
								<tr>
									<td><b>min</b></td>
									<td>{this.state.min}</td>
								</tr>
								<tr>
									<td><b>max</b></td>
									<td>{this.state.max}</td>
								</tr>
								<tr>
									<td><b>unit</b></td>
									<td>{this.state.unit}</td>
								</tr>
								<tr>
									<td><b>allowed</b></td>
									<td>{this.getAllowedList()}</td>
								</tr>
								<tr>
									<td><b>comment</b></td>
									<td>{this.state.comment}</td>
								</tr>
								<tr>
									<td><b>deprecation</b></td>
									<td>{this.state.deprecation}</td>
								</tr>
								<tr>
									<td><b>VSS Signal</b></td>
									{ this.state.type === "branch" ?
									<td>-</td>
									:
									<td>
										<span><b>{this.state.name}</b></span>
										<span>&emsp;</span>
										{ this.renderVssValueField() }
										<span>&emsp;</span>
										<button onClick={() => this.setVssValue(this.state.path)} >
											Set
										</button>
										<span>&emsp;</span>
										<button onClick={() => this.getVssValue(this.state.path)} >
											Get
										</button>
									</td>
									}
								</tr>
								<tr>
									<td><b>CAN Signal</b></td>
									<td>
										<span><b>{this.state.mapping}</b></span>
										<span>&emsp;</span>
										{ this.state.mapping && this.state.mapping !== "-" && this.canSignalMap.has(this.state.mapping) &&
											<>
												{ this.renderCanValueField(this.state.mapping) }
											</>
										}
									</td>
								</tr>
								</tbody>
							</table>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

function EcuSignal(props) {
	const initPhysicalValue = props.signal.initValue;
	const initRawValue = physToRaw(initPhysicalValue, props.signal);
	const [physicalValue, setPhysicalValue] = useState(Number(initPhysicalValue));

	const update = (name, value = physicalValue) => {
		let valueToSend = value;
		if (props.signal.length <= 32) {
			valueToSend = Number(value);
		}
		
		const jsonData = {
			signals: [ 
				{ 
					name  : name, 
					value : valueToSend,
					type  : SIGNAL_TYPES.RAW
				} 
			]
		};
		
		setSignals(jsonData);
	}

	function getRange(signal) {
		let min = 0;
		let max;
		if (signal.length <= 32) {
			const enumSizeMax = 1 << signal.length;
			const enumSize = signal.valueDescAry.length;

			if (signal.isSigned && enumSize < enumSizeMax) {
				if (signal.length < 32) {
					max = (1 << (signal.length - 1)) - 1;
					min = -(max + 1);
				}
				else {
					max = 0x7FFFFFFF;
					min = -(0x80000000);
				}
			}
			else {
				if (signal.length < 32) {
					max = (1 << signal.length) - 1;
				}
				else {
					max = 0xFFFFFFFF;
				}
			}

			if (signal.factor !== 1) {
				max *= signal.factor;
				min *= signal.factor;
			}
			if (signal.offset !== 0) {
				max += signal.offset;
				min += signal.offset;
			}
			if (signal.factor !== 1) {
				max = Number(Number(max).toFixed(5));
				min = Number(Number(min).toFixed(5));
			}
		}
		else {
			max = 0xFFFFFFFFFFFFFFFFn;
			let div = BigInt(1 << (64 - signal.length));
			max /= div;
		}

		let range = min + " ~ " + max;

		return range;
	}

	function setRawValue(value) {
		const rawVal = String(value).split(" : ")[0];
		let physVal = Number(rawVal) * Number(props.signal.factor) + Number(props.signal.offset);
		physVal = Number(physVal.toFixed(5));
		update(props.signal.name, physVal);
	}

	function setRawValue2(event) {
		const options = event.target.getElementsByTagName("option");
		if (options.length === 1) {
			setRawValue(event.target.value);
		}
	}

	return (
		<>
			<input
				type="text" id={"can-value-text"}
				defaultValue={physicalValue}
				onChange={(e) => { setPhysicalValue(e.target.value); }}
				onKeyDown={(e) => {
					if (e.key === 'Enter') {
						update(props.signal.name);
					}
				}}
			>
			</input>
			<span>&emsp;</span>
			<span id={"can-raw-value-text"}>{initRawValue}</span>
			<span>&emsp;</span>
			{
			Object.keys(props.signal.valueDescAry).length > 0 &&
				<select 
					id={"can-value-combo"} 
					onChange={(e) => {setRawValue(e.target.value)}} 
					onFocus={(e) => {setRawValue2(e)}}
				>
				{
					Object.keys(props.signal.valueDescAry).map((key) => 
						<option key={key}>
						{key.substring(5) + " : " + props.signal.valueDescAry[key]}
						</option>
					)
				}
				</select>
			}
			<span>&emsp;</span>
			<button onClick={() => update(props.signal.name)}>
				Set
			</button>
			<span>&emsp;</span>
			<span>{getRange(props.signal)}</span>
		</>
	);
}

function physToRaw(physVal, signal) {
	let rawVal = physVal;
	if (signal.factor > 0) {
		rawVal -= signal.offset;
		rawVal /= signal.factor;
		rawVal = Math.round(rawVal.toString());
	}
	return rawVal;
}


export default memo(VssTree);