import SocketIOClient from 'socket.io-client';


const socket = SocketIOClient("http://localhost:" + process.env.REACT_APP_IO_PORT);

export const SIGNAL_TYPES = {
    RAW: "raw", // CAN
    VSS: "vss", // COVESA Vehicle Signal Spec
    EXT: "ext"  // Extended
};

export function getSignals(jsonData, callback = null) {
	socket.emit("get", jsonData);
	if (callback) {
		socket.off("get");
		socket.on("get", callback);
	}
}

export function setSignals(jsonData, callback = null) {
	socket.emit("set", jsonData);
    if (callback) {
        socket.off("set");
        socket.on("set", callback);
    }
};

export const subscribeSignals = (jsonData, callback = null) => {
	socket.emit("subscribe", jsonData);
    if (callback) {
		socket.off("notify/" + jsonData.channel);
        socket.on("notify/" + jsonData.channel, callback);
    }
}

export const unsubscribeSignals = (jsonData) => {
	socket.emit("unsubscribe", jsonData);
    socket.off("notify/" + jsonData.channel);
}